.profile__name{
    max-width: 294px;
    font-size: 42px;
    line-height: 48px;
    font-weight: 500;
    margin: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media (max-width: 425px){
    .profile__name{
        max-width: 189px;
        font-size: 27px;
        line-height: 33px;
        text-align: center;
    }
}