.table{
    width: 880px;
    margin: 0 auto;
    padding-top: 47px;
}

@media (max-width: 1024px){
    .table{
        max-width: calc(100% - (0px + 256 * ((100vw - 425px) / 599)));
    }
}

@media (max-width: 425px){
    .table{
        max-width: 282px;
        padding-top: 36px;
    }
}